export const slider = () => {
  // First Swiper
  new Swiper(".swiper", {
    slidesPerView: "auto",
    spaceBetween: 20,
    loop: true,
    centeredSlides: false,
    navigation: {
      nextEl: ".swiper-button-next",
      prevEl: ".swiper-button-prev",
    },
    pagination: {
      el: ".swiper-pagination",
      clickable: true,
    },
    autoplay: {
      delay: 2000,
      disableOnInteraction: false,
    },
    breakpoints: {
      768: { slidesPerView: 2 },
      1024: { slidesPerView: 2 },
      1280: { slidesPerView: 4 },
    },
    on: {
      init: function () {
        console.log("First Swiper initialized");
      },
      slideChangeTransitionEnd: function () {
        console.log("First Swiper: Slide changed");
      },
    },
  });

  // Second Swiper
  new Swiper(".swiper-container", {
    slidesPerView: "auto",
    spaceBetween: 20,
    loop: true,
    centeredSlides: true,
    pagination: {
      el: ".swiper-container-pagination", // Use a unique pagination selector
      clickable: true,
    },
    autoplay: {
      delay: 2000,
      disableOnInteraction: false,
    },
    breakpoints: {
      768: { slidesPerView: 2 },
      1024: { slidesPerView: 2 },
      1280: { slidesPerView: 4 },
    },
    on: {
      init: function () {
        console.log("Second Swiper initialized");
      },
      slideChangeTransitionEnd: function () {
        console.log("Second Swiper: Slide changed");
      },
    },
  });
};
